import React from "react";
import { connect } from "react-redux";
import "./style.css";

import YobIcon from "@material-ui/icons/Cake";
import HomeIcon from "@material-ui/icons/Home";
import PhoneIcon from "@material-ui/icons/Phone";
import MailIcon from "@material-ui/icons/Mail";
import LanguageIcon from "@material-ui/icons/Language";
import Helmet from "react-helmet";

import ReactHtmlParser from "react-html-parser";
import { savePDF } from "@progress/kendo-react-pdf";

class Scaffold1 extends React.Component {
  state = { person: {} };
  componentDidMount() {
    if (this.props.person.state.color_background) {
      document.getElementById(
        "sc1"
      ).style.backgroundColor = this.props.person.state.color_background;
    }

    if (this.props.person.state.color_bonus) {
      let array = document.getElementsByClassName("colorbonus");
      Array.from(array).forEach(element => {
        element.style.backgroundColor = this.props.person.state.color_bonus;
      });
    }

    if (this.props.person.state.color_font) {
      let array = document.getElementsByClassName("colorfont");
      Array.from(array).forEach(element => {
        element.style.color = this.props.person.state.color_font;
      });
    }

    if (this.props.person.state.color_main) {
      let array = document.getElementsByClassName("colormain");
      Array.from(array).forEach(element => {
        element.style.backgroundColor = this.props.person.state.color_main;
      });
    }

    savePDF(document.body);
  }
  renderYob() {
    if (this.props.person.state.yob) {
      return (
        <div class="my-info-item colormain colorfont">
          <YobIcon />
          &nbsp; {this.props.person.state.yob}
        </div>
      );
    }
  }

  renderAdress() {
    if (this.props.person.state.address) {
      return (
        <div className="my-info-item colormain colorfont">
          <HomeIcon />
          &nbsp; {this.props.person.state.address}
        </div>
      );
    }
  }

  renderPhonenumber() {
    if (this.props.person.state.phonenumber) {
      return (
        <div className="my-info-item colormain colorfont">
          <PhoneIcon />
          &nbsp; {this.props.person.state.phonenumber}
        </div>
      );
    }
  }

  renderMail() {
    if (this.props.person.state.mail) {
      return (
        <div className="my-info-item colormain colorfont">
          <MailIcon />
          &nbsp; {this.props.person.state.mail}
        </div>
      );
    }
  }

  renderSite() {
    if (this.props.person.state.site) {
      return (
        <div className="my-info-item colormain colorfont">
          <LanguageIcon />
          &nbsp;{" "}
          <a className="colorfont" href={this.props.person.state.site}>
            {this.props.person.state.site}
          </a>
        </div>
      );
    }
  }

  renderInterested() {
    if (this.props.person.state.interested) {
      let variable = "";
      this.props.person.state.interested.forEach(element => {
        variable += `<div class="my-info-item colormain colorfont size">${element}</div>`;
      });

      return ReactHtmlParser(variable);
    }
  }

  renderSkills() {
    if (this.props.person.state.skills) {
      let variable = "";
      this.props.person.state.skills.forEach(element => {
        variable += `<div class="my-info-item colormain colorfont size">${element}</div>`;
      });

      return ReactHtmlParser(variable);
    }
  }

  renderLanguages() {
    if (this.props.person.state.languages) {
      let variable = "";
      this.props.person.state.languages.forEach(element => {
        variable += `<div class="my-info-item colormain colorfont size">${element}</div>`;
      });

      return ReactHtmlParser(variable);
    }
  }

  renderEducation() {
    if (this.props.person.state.education) {
      let variable = "";
      this.props.person.state.education.forEach(element => {
        variable += `<div class="my-info-item education colormain colorfont">
        <div class="year colorfont">${element.start} - ${element.end}</div>
        <div class="tit colorfont">
        <div class="a colorfont">
        <b>${element.name}</b>
      </div>
      <div class="b colorfont">
        <b>${element.title}</b>
      </div>
      <div class="d colorfont">
            ${element.education}
          </div>
      <div class="c colorfont">
        ${element.spec}
      </div>
          
        </div>
      </div>`;
      });

      return ReactHtmlParser(variable);
    }
  }

  renderJobs() {
    if (this.props.person.state.jobs) {
      let variable = "";
      this.props.person.state.jobs.forEach(element => {
        variable += `<div class="my-info-item education colormain colorfont size">`;

        if (element.start && element.end) {
          variable += `<div class="year">${element.start} - ${element.end}</div>`;
        } else {
          if (element.start || element.end) {
            variable += `<div class="year">${element.start} ${element.end}</div>`;
          }
        }

        variable += `<div class="tit">
          
          <div class="d">
          <b>${element.title}</b>
            <div><b>${element.job}</b></div>
            <div>${element.rules}</div>
          </div>
        </div>
      </div>`;
      });

      return ReactHtmlParser(variable);
    }
  }

  personalDataifNotNull = () => {
    if (
      this.props.person.state.yob ||
      this.props.person.state.site ||
      this.props.person.state.mail ||
      this.props.person.state.address ||
      this.props.person.state.phonenumber
    ) {
      return (
        <div className="title colorbonus colorfont size">
          <b>Dane osobowe:</b>
        </div>
      );
    }
  };

  skillsifNotNull = () => {
    if (this.props.person.state.skills.length > 0) {
      return (
        <div className="my-info-item next colorbonus colorfont size">
          <b>Umiejętności:</b>
        </div>
      );
    }
  };

  jobsifNotNull = () => {
    if (this.props.person.state.jobs.length > 0) {
      return (
        <div className="title colorbonus colorfont size">
          <b>Doświadczenie Zawodowe:</b>
        </div>
      );
    }
  };

  educationifNotNull = () => {
    if (this.props.person.state.education.length > 0) {
      return (
        <div className="title title-space colorbonus colorfont size">
          <b>Edukacja:</b>
        </div>
      );
    }
  };

  interestedifNotNull = () => {
    if (this.props.person.state.interested.length > 0) {
      return (
        <div className="title title-space colorbonus colorfont size">
          <b>Zainteresowania:</b>
        </div>
      );
    }
  };

  languagesifNotNull = () => {
    if (this.props.person.state.languages.length > 0) {
      return (
        <div className="my-info-item title title-space colorbonus colorfont size">
          <b>Języki obce:</b>
        </div>
      );
    }
  };

  imageifNotNull = () => {
    if (this.props.person.state.image) {
      return <img src={this.props.person.state.image} alt="me" />;
    }
  };

  render() {
    return (
      <div className="scaffold-1" id="sc1">
        <Helmet>
          <title>{this.props.person.state.cv_title}</title>
        </Helmet>
        <div className="navbar colormain">
          <div className="name colorfont">
            {this.props.person.state.name_surname}
          </div>
          <div className="short-info colorfont">
            {this.props.person.state.description_name_surname}
          </div>
        </div>

        <div className="main">
          <div className="first-col">
            {this.imageifNotNull()}

            <div className="my-info ">
              {this.personalDataifNotNull()}

              {this.renderYob()}

              {this.renderAdress()}

              {this.renderPhonenumber()}

              {this.renderMail()}

              {this.renderSite()}

              {this.languagesifNotNull()}

              {this.renderLanguages()}

              {this.skillsifNotNull()}

              {this.renderSkills()}
            </div>
          </div>
          <div className="second-col ">
            {this.jobsifNotNull()}

            {this.renderJobs()}

            {this.educationifNotNull()}

            {this.renderEducation()}

            {this.interestedifNotNull()}

            {this.renderInterested()}
          </div>
        </div>
        <footer className="colorbonus colorfont">
          <p>
            Wyrażam zgodę na przetwarzanie moich danych osobowych w celu
            rekrutacji zgodnie z art. 6 ust. 1 lit. a Rozporządzenia Parlamentu
            Europejskiego i Rady (UE) 2016/679 z dnia 27 kwietnia 2016 r. w
            sprawie ochrony osób fizycznych w związku z przetwarzaniem danych
            osobowych i w sprawie swobodnego przepływu takich danych oraz
            uchylenia dyrektywy 95/46/WE (ogólne rozporządzenie o ochronie
            danych).
          </p>
        </footer>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    person: state.person
  };
};

export default connect(mapStateToProps)(Scaffold1);
