import React from "react";
import "./style.css";
import { connect } from "react-redux";
import ReactHtmlParser from "react-html-parser";
import { fetchPerson } from "../../actions";
import history from "../../history";

import InputColor from "react-input-color";

class Create extends React.Component {
  state = {
    renderSkills: "",
    renderInterested: "",
    renderJobs: "",
    renderEducation: "",
    renderLanguages: "",
    colorMain: "#fc9403",
    colorBonus: "white",
    colorFont: "black",
    image: "",
    file: "",
    imagePreviewUrl: "",
    colorBackground: "",
    data: {}
  };

  skillsAmount = e => {
    e.preventDefault();

    let amount = document.getElementById("amount-skills").value;

    let renderSkills = "";

    for (let i = 0; i < amount; i++) {
      renderSkills += `<div class="item">
      <input id=skill-${i} type="text" name=skill-${i} placeholder="Umiejętność" />
    </div>`;
    }

    this.setState({ renderSkills: renderSkills });
  };

  jobsAmount = e => {
    e.preventDefault();

    let amount = document.getElementById("amount-jobs").value;

    let renderJobs = "";

    for (let i = 0; i < amount; i++) {
      renderJobs += `<div class="item">
      <div class="item item2">
      <input class="inp" id=jobstart-${i} type="text" name=jobstart-${i} placeholder="Początek" />
      <input class="inp" id=jobsend-${i} type="text" name=jobsend-${i} placeholder="Koniec" />
      </div>
      <input id=jobstit-${i} type="text" name=jobstit-${i} placeholder="Nazwa stanowiska" />
      <input id=jobs-${i} type="text" name=jobs-${i} placeholder="Nazwa firmy" />
      <input id=jobsrul-${i} type="text" name=jobsrul-${i} placeholder="Obowiązki" />
    </div>`;
    }
    this.setState({ renderJobs: renderJobs });
  };

  educationAmount = e => {
    e.preventDefault();

    let amount = document.getElementById("amount-education").value;

    let renderEducation = "";

    for (let i = 0; i < amount; i++) {
      renderEducation += `<div class="item">
      <div class="item item2">
      <input class="inp" id=educationstart-${i} type="text" name=educationstart-${i} placeholder="Początek" />
      <input class="inp" id=educationend-${i} type="text" name=educationend-${i} placeholder="Koniec" />
      </div>
      <div class="item">
      <input id=educationname-${i} type="text" name=educationname-${i} placeholder="Uczelnia" />
      </div>
      <div class="item">
      <input id=educationtitle-${i} type="text" name=educationtitle-${i} placeholder="Tytuł naukowy" />
      </div>
      <div class="item">
      <input id=educationspec-${i} type="text" name=educationspec-${i} placeholder="Specjalizacja" />
      </div>
      <div class="item">
      <input id=education-${i} type="text" name=education-${i} placeholder="Inne informacje" />
      </div>
    </div>`;
    }
    this.setState({ renderEducation: renderEducation });
  };

  languagesAmount = e => {
    e.preventDefault();
    let amount = document.getElementById("amount-languages").value;

    let renderLanguages = "";

    for (let i = 0; i < amount; i++) {
      renderLanguages += `<div class="item">
      <input id=languages-${i} type="text" name=languages-${i} placeholder="Język" />
    </div>`;
    }

    this.setState({ renderLanguages: renderLanguages });
  };

  interestedAmount = e => {
    e.preventDefault();

    let amount = document.getElementById("amount-interested").value;

    let renderInterested = "";

    for (let i = 0; i < amount; i++) {
      renderInterested += `<div class="item">
      <input id=interested-${i} type="text" name=interested-${i} placeholder="Zainteresowanie" />
    </div>`;
    }

    this.setState({ renderInterested: renderInterested });
  };

  onSubmit = e => {
    e.preventDefault();
    let data = {
      color_background: this.state.colorBackground,
      color_font: this.state.colorFont,
      color_main: this.state.colorMain,
      color_bonus: this.state.colorBonus,
      cv_title: document.getElementById("cv-title").value,
      name_surname: document.getElementById("name-surname").value,
      description_name_surname: document.getElementById(
        "description-name-surname"
      ).value,
      image: this.state.imagePreviewUrl,
      yob: document.getElementById("yob").value,
      address: document.getElementById("address").value,
      phonenumber: document.getElementById("phonenumber").value,
      mail: document.getElementById("mail").value,
      site: document.getElementById("site").value
    };

    let languages_array = [];

    for (
      let i = 0;
      i < document.getElementById("amount-languages").value;
      i++
    ) {
      languages_array.push(document.getElementById(`languages-${i}`).value);
    }

    let jobs_array = [];

    for (let i = 0; i < document.getElementById("amount-jobs").value; i++) {
      jobs_array.push({
        start: document.getElementById(`jobstart-${i}`).value,
        end: document.getElementById(`jobsend-${i}`).value,
        job: document.getElementById(`jobs-${i}`).value,
        title: document.getElementById(`jobstit-${i}`).value,
        rules: document.getElementById(`jobsrul-${i}`).value
      });
    }

    let education_array = [];

    for (
      let i = 0;
      i < document.getElementById("amount-education").value;
      i++
    ) {
      education_array.push({
        start: document.getElementById(`educationstart-${i}`).value,
        end: document.getElementById(`educationend-${i}`).value,
        education: document.getElementById(`education-${i}`).value,
        name: document.getElementById(`educationname-${i}`).value,
        title: document.getElementById(`educationtitle-${i}`).value,
        spec: document.getElementById(`educationspec-${i}`).value
      });
    }

    let interested_array = [];
    for (
      let i = 0;
      i < document.getElementById("amount-interested").value;
      i++
    ) {
      interested_array.push(document.getElementById(`interested-${i}`).value);
    }

    let skills_array = [];
    for (let i = 0; i < document.getElementById("amount-skills").value; i++) {
      skills_array.push(document.getElementById(`skill-${i}`).value);
    }

    data.skills = skills_array;
    data.interested = interested_array;
    data.jobs = jobs_array;
    data.education = education_array;
    data.languages = languages_array;

    if (!data.name_surname) {
      // hardcode z moimi danymi do cv 
    }

    this.props.fetchPerson(data);
    history.push("/scaffold_1");
  };

  setColorMain = e => {
    this.setState({ colorMain: e.hex });
  };

  setColorBonus = e => {
    this.setState({ colorBonus: e.hex });
  };

  setColorFont = e => {
    this.setState({ colorFont: e.hex });
  };

  setColorBackground = e => {
    this.setState({ colorBackground: e.hex });
  };

  onDrop = e => {
    e.preventDefault();
    let reader = new FileReader();
    let file = e.target.files[0];

    reader.onloadend = () => {
      this.setState({
        file: file,
        imagePreviewUrl: reader.result
      });
    };
    reader.readAsDataURL(file);
  };
  render() {
    return (
      <div>
        <form className="">
          <div className="item">
            <label>Tytuł:</label>
            <input
              id="cv-title"
              type="text"
              name="cv-title"
              placeholder="Tytuł dokumentu:"
            />
          </div>

          <div className="item">
            <label>Kolor tła:</label>
            <InputColor
              className="color"
              initialHexColor="#ffffff"
              onChange={this.setColorBackground}
              placement="right"
            />
          </div>

          <div className="item">
            <label>Kolor czcionki:</label>
            <InputColor
              className="color"
              initialHexColor="#000000"
              onChange={this.setColorFont}
              placement="right"
            />
          </div>

          <div className="item">
            <label>Kolor główny:</label>
            <InputColor
              className="color"
              initialHexColor="#fc9403"
              onChange={this.setColorMain}
              placement="right"
            />
          </div>

          <div className="item">
            <label>Kolor dodatkowy:</label>
            <InputColor
              className="color"
              initialHexColor="#ffffff"
              onChange={this.setColorBonus}
              placement="right"
            />
          </div>

          <div className="item">
            <label>Imie i nazwisko:</label>
            <input
              id="name-surname"
              type="text"
              name="first-name"
              placeholder="Imie i nazwisko:"
            />
          </div>

          <div className="item">
            <label>Podpis pod nazwiskiem:</label>
            <textarea
              id="description-name-surname"
              type="text"
              name="description-name-surname"
              placeholder="Podpis pod nazwiskiem:"
            />
          </div>

          <div className="item">
            <label>Zdjęcie:</label>

            <input
              type="file"
              onChange={e => this.onDrop(e)}
              id="image"
              name="image"
              placeholder="Zdjęcie:"
            />
          </div>

          <div className="item">
            <label>Data urodzenia:</label>
            <input
              id="yob"
              type="text"
              name="yob"
              placeholder="Data urodzenia:"
            />
          </div>

          <div className="item">
            <label>Adres:</label>
            <input
              id="address"
              type="text"
              name="address"
              placeholder="Adres:"
            />
          </div>

          <div className="item">
            <label>Telefon:</label>
            <input
              id="phonenumber"
              type="text"
              name="phonenumber"
              placeholder="Telefon:"
            />
          </div>

          <div className="item">
            <label>Mail:</label>
            <input id="mail" type="text" name="mail" placeholder="Mail:" />
          </div>

          <div className="item">
            <label>Strona:</label>
            <input id="site" type="text" name="site" placeholder="Strona:" />
          </div>

          <div className="item">
            <label>Języki obce:</label>
            <div className="item-1">
              <input
                id="amount-languages"
                type="text"
                name="amount-languages"
                placeholder="Ilość pól:"
              />
              <button onClick={this.languagesAmount}>OK</button>
            </div>
          </div>

          {ReactHtmlParser(this.state.renderLanguages)}

          <div className="item">
            <label>Umiejętności:</label>
            <div className="item-1">
              <input
                id="amount-skills"
                type="text"
                name="amount-skills"
                placeholder="Ilość pól:"
              />
              <button onClick={this.skillsAmount}>OK</button>
            </div>
          </div>

          {ReactHtmlParser(this.state.renderSkills)}

          <div className="item">
            <label>Zainteresowania:</label>
            <div className="item-1">
              <input
                id="amount-interested"
                type="text"
                name="amount-interested"
                placeholder="Ilość pól:"
              />
              <button onClick={this.interestedAmount}>OK</button>
            </div>
          </div>

          {ReactHtmlParser(this.state.renderInterested)}

          <div className="item">
            <label>Doświadczenie zawodowe:</label>
            <div className="item-1">
              <input
                id="amount-jobs"
                type="text"
                name="amount-jobs"
                placeholder="Ilość pól:"
              />
              <button onClick={this.jobsAmount}>OK</button>
            </div>
          </div>

          {ReactHtmlParser(this.state.renderJobs)}

          <div className="item">
            <label>Edukacja:</label>
            <div className="item-1">
              <input
                id="amount-education"
                type="text"
                name="amount-education"
                placeholder="Ilość pól:"
              />
              <button onClick={this.educationAmount}>OK</button>
            </div>
          </div>

          {ReactHtmlParser(this.state.renderEducation)}

          <button onClick={this.onSubmit}>Zatwierdź dane</button>
        </form>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    person: state.person
  };
};

export default connect(mapStateToProps, { fetchPerson })(Create);
