import React from "react";
import { Router, Route } from "react-router-dom";
import Create from "./Components/create";
import Scaffold1 from "./Components/scaffold-1";

import history from "../src/history";
import "./style.css";

class App extends React.Component {
  redirect = () => {
    history.push("/");
  };

  gotoCreate = () => {
    history.push("/create");
  };

  render() {
    return (
      <Router history={history}>
        <Route path="/" exact>
          <div className="App">
            <div className="navbar" onClick={this.redirect}>
              <div className="title">CV Generator</div>
            </div>

            <div className="main">
              <div className="a"></div>
              <div className="b"></div>
              <div className="c">
                <div className="btn" onClick={this.gotoCreate}>
                  Zaczynajmy
                </div>
              </div>
            </div>

            <footer>Mikołaj Sojka</footer>
          </div>
        </Route>

        <Route path="/create">
          <div className="App">
            <div className="navbar" onClick={this.redirect}>
              <div className="title">CV Generator</div>
            </div>
            <Create />
            <footer>Mikołaj Sojka</footer>
          </div>
        </Route>

        <Route path="/scaffold_1">
          <Scaffold1 />
        </Route>
      </Router>
    );
  }
}

export default App;
